import { Card, CardContent, CardMedia, Link } from "@mui/material";

import {
  CardContentSX,
  CardInfo,
  CardSX,
  CtaInfoText,
} from "Containers/BusinessSettings/LayoutComponents";

const OffStatus = () => {
  return (
    <Card elevation={0} sx={CardSX}>
      <CardContent
        sx={{
          ...CardContentSX,
          pt: 0,
          alignItems: "center",
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          rowGap: 5.25,
          textAlign: "center",
        }}
      >
        <CardMedia
          component="img"
          image="/static/images/svg/inactive.svg"
          alt="inactive-wa"
          sx={{ width: "auto", mx: "auto" }}
        />
        <CardInfo>
          <CtaInfoText>
            {
              "Your WhatsApp Business API account is not active yet. It usually takes upto 72 business hours for activation."
            }
          </CtaInfoText>
          <CtaInfoText>
            {"Please contact us on "}
            <Link href="mailto:support@easysocial.io" underline="hover">
              {"support@easysocial.io"}
            </Link>
            {" in case it is taking longer than expected."}
          </CtaInfoText>
        </CardInfo>
      </CardContent>
    </Card>
  );
};

export default OffStatus;
