import { useState } from "react";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  TextField,
  TextFieldProps,
} from "@mui/material";
import moment from "moment";
import { AdapterMoment } from "@mui/x-date-pickers/AdapterMoment";
import { DatePicker, LocalizationProvider } from "@mui/x-date-pickers";

import { OlderReportsProps } from "./types";

const OlderReports = ({
  open,
  closeDialog,
  downloadReport,
  downloading,
  setDownloading,
}: OlderReportsProps) => {
  const [month, setMonth] = useState<Date | null>(null);

  return (
    <Dialog open={open} onClose={closeDialog}>
      <DialogTitle>{"Select Month"}</DialogTitle>
      <DialogContent sx={{ pt: "20px !important" }}>
        <LocalizationProvider dateAdapter={AdapterMoment}>
          <DatePicker
            views={["year", "month"]}
            openTo="year"
            label="Year and Month"
            minDate={new Date("2021-01-01")}
            maxDate={new Date()}
            value={month}
            onChange={(value: Date | null) => {
              setMonth(value);
            }}
            renderInput={({ inputProps, ...params }: TextFieldProps) => (
              <TextField
                {...params}
                helperText={null}
                inputProps={{
                  ...inputProps,
                  ...(month
                    ? { value: moment(month).local().format("MMM YYYY") }
                    : {}),
                }}
                autoComplete="off"
              />
            )}
          />
        </LocalizationProvider>
      </DialogContent>
      <DialogActions>
        <Button
          disabled={downloading}
          onClick={() => {
            setDownloading(true);
            downloadReport(moment(month).local().format("YYYY-MM"));
          }}
        >
          Download
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default OlderReports;
