import { Fragment } from "react";
import { Dialog, DialogContent, TextField, Box } from "@mui/material";
import { AdapterDateFns } from "@mui/x-date-pickers/AdapterDateFns";
import {
  StaticDateRangePicker,
  LocalizationProvider,
} from "@mui/x-date-pickers-pro";
import { MuiTextFieldProps } from "@mui/x-date-pickers/internals";

import { RangePopUpProps } from "./types";

const RangePopUp = ({
  open,
  closeDialog,
  range,
  setRange,
  wrapper = "mobile",
}: RangePopUpProps) => {
  return (
    <Dialog
      open={open}
      onClose={(
        _event: {},
        reason: "backdropClick" | "escapeKeyDown" | "closeButtonClick"
      ) => closeDialog(reason)}
      maxWidth="md"
    >
      <DialogContent
        sx={{
          "& .MuiPickerStaticWrapper-content": {
            "& > div": {
              "& > div:first-of-type": {
                display: "none",
              },
            },
          },
        }}
      >
        <LocalizationProvider dateAdapter={AdapterDateFns}>
          <StaticDateRangePicker
            displayStaticWrapperAs={wrapper}
            value={range}
            onChange={(newValue: any) => {
              setRange(newValue);
              const [selectedStart, selectedEnd] = newValue;
              selectedStart !== null &&
                selectedEnd !== null &&
                closeDialog("closeButtonClick");
            }}
            maxDate={new Date()}
            minDate={new Date("2021-01-01")}
            renderInput={(
              startProps: MuiTextFieldProps,
              endProps: MuiTextFieldProps
            ) => (
              <Fragment>
                <TextField {...startProps} />
                <Box sx={{ mx: 2 }}> to </Box>
                <TextField {...endProps} />
              </Fragment>
            )}
          />
        </LocalizationProvider>
      </DialogContent>
    </Dialog>
  );
};

export default RangePopUp;
