import { Fragment, useCallback, useEffect, useState } from "react";
import {
  Card,
  CardContent,
  Link,
  Skeleton,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from "@mui/material";

import BusinessApi from "Apis/BusinessApi";
import { useSnacks } from "Contexts/Snack";
import {
  ActionArea,
  CardContentSX,
  CardHeading,
  CardSX,
} from "Containers/BusinessSettings/LayoutComponents";
import { monthFormat } from "Utils";

import OlderReports from "./OlderReports";

const UtilizationReport = ({ loadingStatus }: { loadingStatus: boolean }) => {
  const { setErrorMessage, setSuccessMessage } = useSnacks();
  const [downloading, setDownloading] = useState<boolean>(false);
  const [oldReports, setOldReports] = useState<boolean>(false);
  const [loadingReport, setLoadingReport] = useState<boolean>(true);
  const [monthlyReport, setMonthlyReport] = useState<any[]>([]);
  const downloadReport = async (month: string) => {
    try {
      const res = await BusinessApi.downloadWaMonthWiseReport({ month });
      if (res) {
        const date = new Date(month);
        const url = window.URL.createObjectURL(new Blob([res]));
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute(
          "download",
          `SessionReport_${date.toLocaleString("en-us", {
            month: "short",
          })}_${date.getFullYear()}.pdf`
        );
        // Append to html link element page
        document.body.appendChild(link);
        link.style.display = "none";
        // Start download
        link.click();
        // Clean up and remove the link
        link?.parentNode?.removeChild(link);
        setSuccessMessage("Report Downloaded");
      } else {
        throw new Error("Internal Server Error");
      }
    } catch (error: any) {
      setErrorMessage(error.message);
    } finally {
      setOldReports(false);
    }
  };
  const fetchMonthlyReport = useCallback(async () => {
    try {
      const res = await BusinessApi.getWaMonthlyReport();
      if (!res.success) {
        throw Error(res.message);
      }
      setMonthlyReport(res.payload);
      setLoadingReport(false);
    } catch (error: any) {
      setErrorMessage(error.message);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    fetchMonthlyReport();
  }, [fetchMonthlyReport]);

  return (
    <Fragment>
      <Card elevation={0} sx={CardSX}>
        <CardContent sx={CardContentSX}>
          <CardHeading>
            {!loadingStatus ? (
              "Monthly Utilization Report"
            ) : (
              <Skeleton width={214} />
            )}
          </CardHeading>
          <TableContainer>
            <Table
              aria-label="Monthly Report"
              className="reports"
              sx={{
                "& .MuiTableCell-root": {
                  borderBottom: "none",
                  color: "#3B414A",
                  fontSize: "14px",
                  lineHeight: "16px",
                },

                "& .MuiLink-root": {
                  alignItems: "center",
                  color: "#32C5FF",
                  display: "inline-flex",
                  fontSize: "14px",
                  fontWeight: 500,
                  lineHeight: "16px",
                },

                "& .MuiTableHead-root": {
                  "& .MuiTableCell-root": {
                    fontWeight: 700,
                    opacity: 0.7,
                    padding: { xs: "0 0 14px", sm: "8px" },
                  },
                },

                "& .MuiTableBody-root": {
                  "& .MuiTableRow-root": {
                    "&.MuiTableRow-hover": {
                      "&:hover": {
                        backgroundColor: "transparent",

                        "& .MuiTableCell-root": {
                          backgroundColor: "#C4C4C433",

                          "&:first-of-type": {
                            borderRadius: "10px 0 0 10px",
                          },

                          "&:last-of-type": {
                            borderRadius: "0 10px 10px 0",
                          },
                        },
                      },
                    },

                    "& .MuiTableCell-root": {
                      borderBottom: "none",
                      fontWeight: 400,
                      height: "32px",
                      opacity: 0.9,

                      "&:not(.loading)": {
                        padding: 0,
                      },

                      "&:last-of-type": {
                        "& .MuiSkeleton-root": {
                          backgroundColor: "#32C5FF80",
                        },
                      },

                      "& .MuiSkeleton-root": {
                        backgroundColor: "#3B414A",
                        margin: "0 auto",
                      },
                    },
                  },
                },
              }}
            >
              <TableHead>
                <TableRow>
                  <TableCell align="center">{"Duration"}</TableCell>
                  <TableCell align="center">
                    {"Customer"}
                    <br />
                    {"Initiated Sessions"}
                  </TableCell>
                  <TableCell align="center">
                    {"Business"}
                    <br />
                    {"Initiated Sessions"}
                  </TableCell>
                  <TableCell align="center">{"Report"}</TableCell>
                </TableRow>
              </TableHead>
              {loadingReport ? (
                <TableBody>
                  <TableRow
                    sx={{
                      "&:last-of-type td, &:last-of-type th": {
                        border: 0,
                      },
                    }}
                    hover
                  >
                    <TableCell align="center">
                      <Skeleton width={60} />
                    </TableCell>
                    <TableCell align="center">
                      <Skeleton width={24} />
                    </TableCell>
                    <TableCell align="center">
                      <Skeleton width={24} />
                    </TableCell>
                    <TableCell align="center">
                      <Skeleton width={74} />
                    </TableCell>
                  </TableRow>
                  <TableRow
                    sx={{
                      "&:last-of-type td, &:last-of-type th": {
                        border: 0,
                      },
                    }}
                    hover
                  >
                    <TableCell align="center">
                      <Skeleton width={60} />
                    </TableCell>
                    <TableCell align="center">
                      <Skeleton width={24} />
                    </TableCell>
                    <TableCell align="center">
                      <Skeleton width={24} />
                    </TableCell>
                    <TableCell align="center">
                      <Skeleton width={74} />
                    </TableCell>
                  </TableRow>
                  <TableRow
                    sx={{
                      "&:last-of-type td, &:last-of-type th": {
                        border: 0,
                      },
                    }}
                    hover
                  >
                    <TableCell align="center">
                      <Skeleton width={60} />
                    </TableCell>
                    <TableCell align="center">
                      <Skeleton width={24} />
                    </TableCell>
                    <TableCell align="center">
                      <Skeleton width={24} />
                    </TableCell>
                    <TableCell align="center">
                      <Skeleton width={74} />
                    </TableCell>
                  </TableRow>
                </TableBody>
              ) : monthlyReport && monthlyReport.length === 0 ? (
                <TableBody>
                  <TableRow>
                    <TableCell colSpan={4} align="center">
                      {"No Reports Available"}
                    </TableCell>
                  </TableRow>
                </TableBody>
              ) : (
                <TableBody>
                  {monthlyReport &&
                    monthlyReport.map((report, index) => (
                      <TableRow
                        key={index}
                        sx={{
                          "&:last-of-type td, &:last-of-type th": {
                            border: 0,
                          },
                        }}
                        hover
                      >
                        <TableCell align="center">
                          {monthFormat(report.month)}
                        </TableCell>
                        <TableCell align="center">{report.cis}</TableCell>
                        <TableCell align="center">{report.bis}</TableCell>
                        <TableCell align="center">
                          <Link
                            component="button"
                            underline="none"
                            className="report-download"
                            onClick={() => downloadReport(report.month)}
                          >
                            <img
                              src="/static/images/svg/download.svg"
                              alt="download"
                            />
                            <span className="ms-1">{"Download"}</span>
                          </Link>
                        </TableCell>
                      </TableRow>
                    ))}
                </TableBody>
              )}
            </Table>
          </TableContainer>
          <ActionArea sx={{ my: 2.5 }}>
            {loadingReport ? (
              <Skeleton width={180} sx={{ backgroundColor: "#32C5FF80" }} />
            ) : (
              <Link
                component="button"
                underline="none"
                sx={{
                  color: "#32C5FF",
                  fontSize: 16,
                  fontWeight: 600,
                  lineHeight: 1.1875,
                }}
                onClick={() => setOldReports(true)}
              >
                Download older reports
              </Link>
            )}
          </ActionArea>
        </CardContent>
      </Card>
      <OlderReports
        open={oldReports}
        closeDialog={() => {
          setOldReports(false);
        }}
        downloading={downloading}
        setDownloading={setDownloading}
        downloadReport={downloadReport}
      />
    </Fragment>
  );
};

export default UtilizationReport;
