import { useCallback, useEffect, useState } from "react";
import { Card, CardContent, Skeleton } from "@mui/material";

import {
  BlockLabel,
  BlockLabelWrap,
  BlockTitle,
  CardContentSX,
  CardHeading,
  CardSX,
  FooterNote,
  StatusBlock,
  StatusBlocks,
} from "Containers/BusinessSettings/LayoutComponents";
import { useSnacks } from "Contexts/Snack";
import { WaConnection, WaHealth, WaTiers } from "Shared/Enums/BusinessSettings";
import BusinessApi from "Apis/BusinessApi";

const LiveStatus = ({ loadingStatus }: { loadingStatus: boolean }) => {
  const { setErrorMessage } = useSnacks();
  const [tier, setTier] = useState<WaTiers | undefined>();
  const [connection, setConnection] = useState<WaConnection | undefined>();
  const [wAHealth, setWAHealth] = useState<WaHealth | undefined>();
  const TierLabel = () =>
    tier ? (
      <BlockLabelWrap>
        <svg
          width="11"
          height="11"
          viewBox="0 0 11 11"
          fill="none"
          xmlns="//www.w3.org/2000/svg"
        >
          <circle
            cx="5.5"
            cy="5.5"
            r={0.5 + tier.size}
            fill={tier.statusColor}
          />
          <circle cx="5.5" cy="5.5" r="5" stroke="#B9B9B9" />
        </svg>
        <BlockLabel>{tier.name}</BlockLabel>
      </BlockLabelWrap>
    ) : (
      <BlockLabelWrap>
        <Skeleton width={58} />
      </BlockLabelWrap>
    );
  const StatusLabel = () =>
    wAHealth ? (
      <BlockLabelWrap>
        {wAHealth.health === "GREEN" && (
          <svg
            width="14"
            height="12"
            viewBox="0 0 14 12"
            fill="none"
            xmlns="//www.w3.org/2000/svg"
          >
            <path
              fillRule="evenodd"
              clipRule="evenodd"
              d="M13.0134 3.10551L4.11073 11.2663L0.767578 6.44966L3.2321 4.73908L4.61622 6.73324L10.9863 0.894043L13.0134 3.10551Z"
              fill={wAHealth.statusColor}
            />
          </svg>
        )}
        <BlockLabel>{wAHealth.status}</BlockLabel>
      </BlockLabelWrap>
    ) : (
      <BlockLabelWrap>
        <Skeleton width={55} />
      </BlockLabelWrap>
    );
  const ConnectionLabel = () =>
    connection ? (
      <BlockLabelWrap>
        {connection.status === "connected" ? (
          <svg
            width="14"
            height="12"
            viewBox="0 0 14 12"
            fill="none"
            xmlns="//www.w3.org/2000/svg"
          >
            <path
              fillRule="evenodd"
              clipRule="evenodd"
              d="M13.0134 3.10551L4.11073 11.2663L0.767578 6.44966L3.2321 4.73908L4.61622 6.73324L10.9863 0.894043L13.0134 3.10551Z"
              fill={connection.statusColor}
            />
          </svg>
        ) : (
          <svg
            width="10"
            height="10"
            viewBox="0 0 10 10"
            fill="none"
            xmlns="//www.w3.org/2000/svg"
          >
            <path
              fillRule="evenodd"
              clipRule="evenodd"
              d="M9.62882 1.98772L2.62882 9.98772L0.371094 8.01221L7.37109 0.012207L9.62882 1.98772Z"
              fill={connection.statusColor}
            />
            <path
              fillRule="evenodd"
              clipRule="evenodd"
              d="M0.371176 1.98772L7.37118 9.98772L9.62891 8.01221L2.62891 0.012207L0.371176 1.98772Z"
              fill={connection.statusColor}
            />
          </svg>
        )}
        <BlockLabel>
          {connection.status === "connected" ? "Connected" : "Not Connected"}
        </BlockLabel>
      </BlockLabelWrap>
    ) : (
      <BlockLabelWrap>
        <Skeleton width={88} />
      </BlockLabelWrap>
    );
  const fetchHealthReport = useCallback(async () => {
    try {
      const res = await BusinessApi.getWaHealthReport();
      if (!res.success) {
        throw Error(res.message);
      }
      const { account_status, health } = res.payload;
      setTier(WaTiers.TIER1);
      switch (health) {
        case "GREY":
          setWAHealth(WaHealth.GREY);
          break;
        case "RED":
          setWAHealth(WaHealth.RED);
          break;
        case "ORANGE":
          setWAHealth(WaHealth.ORANGE);
          break;
        case "GREEN":
          setWAHealth(WaHealth.GREEN);
          break;
      }
      switch (account_status) {
        case "connected":
          setConnection(WaConnection.Connected);
          break;
        default:
          setConnection(WaConnection.NotConnected);
          break;
      }
    } catch (error: any) {
      setErrorMessage(error.message);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    fetchHealthReport();
  }, [fetchHealthReport]);

  return (
    <Card elevation={0} sx={CardSX}>
      <CardContent sx={{ ...CardContentSX, pb: "10px !important" }}>
        <CardHeading>
          {!loadingStatus ? "Live Status" : <Skeleton width={90} />}
        </CardHeading>
        <StatusBlocks
          sx={{
            margin: { xs: "-20px -36px 20px", sm: "-20px -20px 0" },
            padding: { xs: "20px 36px", sm: "20px" },
          }}
        >
          <StatusBlock statusColor={tier?.statusColor}>
            <BlockTitle>{"Messaging Tier"}</BlockTitle>
            <TierLabel />
          </StatusBlock>
          <StatusBlock statusColor={wAHealth?.statusColor}>
            <BlockTitle>{"WhatsApp Health"}</BlockTitle>
            <StatusLabel />
          </StatusBlock>
          <StatusBlock statusColor={connection?.statusColor}>
            <BlockTitle>{"Account Status"}</BlockTitle>
            <ConnectionLabel />
          </StatusBlock>
        </StatusBlocks>
        <FooterNote>
          {tier ? tier.message : <Skeleton width="95%" />}
        </FooterNote>
      </CardContent>
    </Card>
  );
};

export default LiveStatus;
