import { Fragment, useCallback, useEffect, useState } from "react";
import { Card, CardContent, Link, Skeleton } from "@mui/material";
import { DateRange } from "@mui/lab";
import moment from "moment";

import BusinessApi from "Apis/BusinessApi";
import {
  CardContentSX,
  CardHeader,
  CardHeading,
  CardSX,
  GrowColumn,
  PeriodLabel,
  ShrinkColumn,
  UsageBlock,
  UsageBlocks,
  UsageBody,
  UsageHeader,
  UsageTitle,
  UsageUnit,
  UsageValue,
} from "Containers/BusinessSettings/LayoutComponents";
import { useGlobal } from "Contexts/App";
import { useSnacks } from "Contexts/Snack";
import { Business, Customer, Reply } from "Assets/Icons";

import { UtilizationPeriods } from "./enums";
import RangePopUp from "./RangePopUp";

const Utilization = ({ loadingStatus }: { loadingStatus: boolean }) => {
  const { isMobile } = useGlobal();
  const { setErrorMessage } = useSnacks();
  const { TODAY, CUSTOM } = UtilizationPeriods;
  const [loadingUsage, setLoadingUsage] = useState<boolean>(true);
  const [cis, setCIS] = useState<number>(0);
  const [bis, setBIS] = useState<number>(0);
  const [average, setAverage] = useState<number>(0);
  const [period, setPeriod] = useState<UtilizationPeriods>(TODAY);
  const [rangePicker, toggleRangePicker] = useState<boolean>(false);
  const [range, setRange] = useState<DateRange<Date>>([null, null]);
  const [startDate, endDate] = range;
  const openRangePicker = () => {
    setRange([null, null]);
    toggleRangePicker(true);
    setTimeout(() => {
      console.clear();
    }, 100);
  };
  const closeRangePicker = (reason: string) => {
    if (reason === "escapeKeyDown" || reason === "backdropClick") {
      setPeriod(TODAY);
    } else {
      setPeriod(CUSTOM);
    }
    toggleRangePicker(false);
  };
  const getRangeUtilization = useCallback(async () => {
    try {
      const from =
        period === CUSTOM && startDate
          ? startDate
          : period === TODAY
          ? new Date()
          : null;
      const to =
        period === CUSTOM && endDate
          ? endDate
          : period === TODAY
          ? new Date()
          : null;
      from && from.setHours(0, 0, 0, 0);
      to && to.setHours(23, 59, 59, 999);
      if (from && to) {
        setLoadingUsage(true);
        const res = await BusinessApi.getRangeUtilization(from, to);
        if (!res.success) {
          throw Error(res.message);
        }
        setCIS(res.payload.cis);
        setBIS(res.payload.bis);
        setAverage(res.payload.replyRate);
      }
    } catch (error: any) {
      setErrorMessage(error.message);
    } finally {
      setLoadingUsage(false);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [period, startDate, endDate]);

  useEffect(() => {
    getRangeUtilization();
  }, [getRangeUtilization]);

  return (
    <Fragment>
      <Card elevation={0} sx={CardSX}>
        <CardContent sx={CardContentSX}>
          <CardHeader sx={{ mb: { xs: 1.875, sm: 3.75 } }}>
            <GrowColumn
              sx={{
                alignItems: "baseline",
                display: "flex",
                flexDirection: { xs: "column", lg: "row" },
                gap: 0.625,
              }}
            >
              <CardHeading sx={{ mb: 0 }}>
                {!loadingStatus ? "Utilization" : <Skeleton width={90} />}
              </CardHeading>
              {period === "TODAY" && (
                <PeriodLabel>{moment().format("Do MMM YYYY")}</PeriodLabel>
              )}
              {period === "CUSTOM" && startDate && endDate && (
                <PeriodLabel>
                  {moment(startDate).format("Do MMM YYYY")}
                  {` to `}
                  {moment(endDate).format("Do MMM YYYY")}
                </PeriodLabel>
              )}
            </GrowColumn>
            <ShrinkColumn
              sx={{ display: "flex", alignItems: "center", gap: 1.75 }}
            >
              <Link
                component="button"
                underline="none"
                onClick={() => {
                  setPeriod(TODAY);
                  setRange([null, null]);
                }}
                sx={{
                  color: "#82858B",
                  fontSize: 12,
                  fontWeight: 500,
                  lineHeight: "12px",
                  borderBottom:
                    period === TODAY
                      ? "2px solid #12B0EE"
                      : "2px solid transparent",
                }}
              >
                {"TODAY"}
              </Link>
              <Link
                className={`change-period ${
                  period === "CUSTOM" ? "active" : ""
                }`}
                component="button"
                underline="none"
                onClick={() => openRangePicker()}
                sx={{
                  color: "#82858B",
                  fontSize: 12,
                  fontWeight: 500,
                  lineHeight: "12px",
                  borderBottom:
                    period === CUSTOM
                      ? "2px solid #12B0EE"
                      : "2px solid transparent",
                }}
              >
                {"CUSTOM"}
              </Link>
            </ShrinkColumn>
          </CardHeader>
          <UsageBlocks
            sx={{
              py: { xs: 0, sm: 2.5 },
              px: { xs: 2.5 },
              mt: { lg: -2.5 },
              mx: { lg: -2.5 },
              flexDirection: { xs: "column", lg: "row" },
            }}
          >
            <UsageBlock>
              <UsageHeader>
                <UsageTitle>{"Customer Initiated Sessions"}</UsageTitle>
                <Customer />
              </UsageHeader>
              <UsageBody>
                <UsageValue>{loadingUsage ? <Skeleton /> : cis}</UsageValue>
                <UsageUnit>
                  {loadingUsage ? <Skeleton /> : "Sessions"}
                </UsageUnit>
              </UsageBody>
            </UsageBlock>
            <UsageBlock>
              <UsageHeader>
                <UsageTitle>{"Business Initiated Sessions"}</UsageTitle>
                <Business />
              </UsageHeader>
              <UsageBody>
                <UsageValue>{loadingUsage ? <Skeleton /> : bis}</UsageValue>
                <UsageUnit>
                  {loadingUsage ? <Skeleton /> : "Sessions"}
                </UsageUnit>
              </UsageBody>
            </UsageBlock>
            <UsageBlock>
              <UsageHeader>
                <UsageTitle>
                  {"Business Initiated Sessions - Reply %"}
                </UsageTitle>
                <Reply />
              </UsageHeader>
              <UsageBody>
                <UsageValue>{loadingUsage ? <Skeleton /> : average}</UsageValue>
              </UsageBody>
            </UsageBlock>
          </UsageBlocks>
        </CardContent>
      </Card>
      <RangePopUp
        open={rangePicker}
        closeDialog={closeRangePicker}
        range={range}
        setRange={setRange}
        wrapper={isMobile ? "mobile" : "desktop"}
      />
    </Fragment>
  );
};

export default Utilization;
